// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------


.main {

    &__container {
        width: 100%;
        max-width: $max-width;
        padding: 1rem;

        @media only screen and (min-width: map-get($breakpoints, large)) {
            padding: 3rem 10rem;
        }
    }
}


.highlighted-article {

    &__container {
        position: relative;
    }

    &__link {
        position: absolute;
        bottom: 0;
        right: .5rem;
        left: .5rem;
        padding: .5rem;
        text-align: right;
        color: map-get($theme-colors, light);

        @media only screen and (min-width: map-get($breakpoints, medium)) {
            bottom: 1rem;
            right: 1rem;
            left: 1rem;
            padding: 1rem;
        }

        h3 {
            padding-bottom: 0.5rem;
            transition: box-shadow 0.5s ease;
        }

        p {
            margin-bottom: 0;
            transition: color 0.5s ease;
        }

        &:hover {
            h3 {
                box-shadow: 0px 3px 0px 0px #fff;
            }

            p {
                color: map-get($theme-colors, primary);
            }
        }
    }

    &__img {
        width: 100%;
        filter: brightness(0.7);
    }
}


.articles {
    margin-top: 4rem;

    &__container {}

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;


    }

    &__list-item {
        margin-bottom: 4rem;

        img {
            width: 100%;
            max-width: 14rem;
        }

        p {
            font-family: $regular-secondary;
            line-height: 1.6;
            max-width: 30rem;
            min-height: 6.34rem;
        }



    }

    &__list-item-title {
        font-size: 1.6rem;
        font-family: $light;

        strong {
            font-family: $bold;
        }
    }

    &__list-item-subtitle {
        font-size: 0.8rem;
        min-height: auto !important;
        // este important se lo debemos a cómo renderiza jekyll los parrafos...
    }

    &__list-item-text,
    p {
        font-family: $regular-secondary;
        line-height: 1.6;
        max-width: 30rem;
        min-height: 6.34rem;
    }

    &__list-item-link {
        display: block;
        color: map-get($theme-colors, dark);
        font-family: $bold;
        padding: .4rem;
        box-shadow: 0px 3px 0px 0px map-get($theme-colors, primary);
        transition: box-shadow 0.5s ease;

        &:hover {
            box-shadow: 0px 0px 0px 3px $gray-600;
        }
    }
}


.footer {
    padding: 1rem;
    background-color: map-get($theme-colors, dark);

    &__container {}
}



// estilos artículo

.article {

    &__subtitle {
        font-size: 0.8rem;
    }

    &__summary {
        font-family: $regular;
        font-size: 1.3rem;
        line-height: 1.4;
    }

    &__text,
    p {
        font-family: $regular-secondary;
        line-height: 1.46;
        max-width: 44rem;

        &:first-of-type:not(.article__subtitle) {

            &:first-letter {
                font-size: 200%;
            }
        }

        p {

        }


    }
}

hr {
    border: 2px solid map-get($theme-colors, primary);
}