// -----------------------------------------------------------------------------
// This file contains all application-wide Sass variables.
// -----------------------------------------------------------------------------




// Color system
$white:    #fff !default;
$black:    #000 !default;

$gray-100: #fbfbfb !default;
$gray-200: #ebebeb !default;
$gray-300: #babdc6 !default;
$gray-400: #8a8793 !default;
$gray-500: #6c6b76 !default;
$gray-600: #434d57 !default;
$gray-800: #3c3c3b !default;


$blue:     #53caff !default;
$red:      #B90000 !default;
$yellow:   #FFDC00 !default;
$green:    #20c997 !default;

$bg-gray: $gray-200 !default;
$overlay-color: $gray-100 !default;


$theme-colors: (
  primary: $blue,
  secondary: $gray-300,
  terciary: $white,
  success: $green,
  info: $black,
  warning: $red,
  danger: $red,
  light: $gray-200,
  dark: $gray-800
) !default;


/// Container's maximum width
/// @type Length
$max-width: 1180px !default;


/// Breakpoints map
/// @prop {String} keys - Keys are identifiers mapped to a given length
/// @prop {Map} values - Values are actual breakpoints expressed in pixels
$breakpoints: (
  'small': 320px,
  'medium': 768px,
  'large': 1024px,
) !default;


// Fonts
// Font, line-height, and color for body text, headings, and more.
$regular:  'hk_guiseregular',"Helvetica Neue", Arial, sans-serif !default;
$bold: "hk_guisebold", "Helvetica Neue", Arial, sans-serif !default;
$light: "hk_guiselight","Helvetica Neue", Arial, sans-serif !default;
$medium: "hk_guisesemibold","Helvetica Neue", Arial, sans-serif !default;
$regular-secondary: "robotoregular","Helvetica Neue", Arial, sans-serif !default;


$text-color: map-get($theme-colors, dark);

/// Relative or absolute URL where all assets are served from
/// @type String
/// @example scss - When using a CDN
///   $base-url: 'http://cdn.example.com/assets/';
$base-url: '/assets/' !default;
