/**
 * Basic typography style for copy text
 */
body {
  color: $text-color;
  font-size: 100%;
  font-family: $regular;
}

h1, h2, h3, h4, h5, h6 {
	 font-family: $bold;
	 letter-spacing: 1px;
	 margin-bottom: 1rem;
}


h1 {
 
  font-size: 1.6rem;
 
  @media only screen and (min-width: map-get($breakpoints, medium)) {
    font-size: 2.6rem;
  }
}

h2 {
  font-size: 1.4rem;

  @media only screen and (min-width: map-get($breakpoints, medium)) {
    font-size: 2.4rem;
  }
}

h3 {
  font-size: 1.2rem;

  @media only screen and (min-width: map-get($breakpoints, medium)) {
    font-size: 2rem;
  }
}